<template>
  <div class="bg">
    <el-carousel indicator-position="none" height="100vh">
      <el-carousel-item v-for="item in hbimg" :key="item.id">
        <img class="hbimg" :src="item.img"/>
      </el-carousel-item>
    </el-carousel>
    <div class="login">
      <span class="guanbi" @click="guanbi()"></span>
      <div class="box">
        <h3>登录管饭账号</h3>
        <!--
        <el-input placeholder="请输入账号" v-model="username" clearable></el-input>
        <el-input placeholder="请输入密码" v-model="password" show-password></el-input>
        <p>
          <a href="javascript:;">验证码登录</a>
          <a href="javascript:;">忘记密码?</a>
        </p>
        <button @click="login" style="cursor:pointer">登录</button>
         -->
        <el-form ref="loginForm" :model="loginForm" :rules="loginRules" status-icon label-width="0px"
                 class="demo-ruleForm">
          <el-form-item prop="name">
            <el-input class="dlinput" placeholder="输入11位手机号码" type="tel" v-model="loginForm.phone" autocomplete="off"
                      clearable></el-input>
          </el-form-item>
          <el-form-item class="dlinput" prop="pass">
            <el-input type="password" placeholder="输入密码(6~20位数字或大小写字母)" v-model="loginForm.password" autocomplete="off"
                      show-password
                      clearable></el-input>
          </el-form-item>
          <el-form-item style="margin-bottom: 0;">
            <router-link style="float: left;" to="/codelogin">验证码登录</router-link>
            <router-link style="float: right;" to="/pwd">忘记密码?</router-link>
          </el-form-item>
          <el-form-item style="margin-bottom: 0;">
            <el-button class="dlbtn" @click="login">登录</el-button>
          </el-form-item>
        </el-form>
        <div class="tongyi text-12">
          <span> 登录/注册即同意</span>
          <el-button type="text" @click="dialogVisible = true">《管饭用户及隐私协议》</el-button>
        </div>
        <el-dialog
          title="管饭用户及隐私协议"
          :visible.sync="dialogVisible"
          width="50%">
          <iframe width="100%" height="400px" src="https://api.guanfan.top/web/about/about.html"></iframe>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="dialogVisible = false">确认同意</el-button>
          </span>
        </el-dialog>
      </div>
      <div class="registe">
        <p>
          <router-link to="/signup">还没有账号？注册管饭账号</router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      const validatePass = (rule, value, callback) => {
        if (value.length < 6) {
          callback(new Error('密码不能小于6位'))
        } else if (value.length > 20) {
          callback(new Error('密码不能大于20位'))
        } else {
          callback()
        }
      };
      return {
        loginForm: {
          phone: '',
          password: ''
        },
        dialogVisible: false,
        loginRules: {
          phone: [{requird: true, trigger: 'blur'}],
          password: [{requird: true, trigger: 'blur', validator: validatePass}]
        },
        hbimg: [
          {
            img: ("https://guanfan.oss-cn-hangzhou.aliyuncs.com/apk/bg1.jpg"),
            id: 0
          },
          {
            img: ("https://guanfan.oss-cn-hangzhou.aliyuncs.com/apk/bg2.jpg"),
            id: 1
          },
          {
            img: ("https://guanfan.oss-cn-hangzhou.aliyuncs.com/apk/bg3.jpg"),
            id: 2
          }
        ],
      };
    },
    methods: {
      login() {
        this.$refs.loginForm.validate(valid => {
          if (valid) {
            this.$store.dispatch('LoginByPsw', this.loginForm).then(() => {
              this.getUser()
            }).catch(() => {

            })
          } else {
            return false
          }
        })
      },
      getUser() {
        this.$store.dispatch('GetUserInfo').then(response => {
          this.$router.push({path: this.redirect || '/'})
        }).catch(error => {
        })
      },
      guanbi() {
        this.$router.push({path: this.redirect || '/'})
      }
    }
  }
</script>


<style scoped>
  .bg {
    height: 100%;
    min-height: 100vh;
  }

  .login {
    position: absolute;
    top: 220px;
    right: 280px;
    width: 400px;
    height: 400px;
    background-color: #fff;
    z-index: 9999;
    border-radius: 6px;
  }

  .box {
    margin: 54px 60px 0;
    text-align: center;
  }

  .box > p {
    display: flex;
    justify-content: space-between;
    padding: 0 12px;
    font-size: 13px;
  }

  .box > p > a:last-child {
    color: #999;
  }

  .dlbtn {
    width: 280px;
    height: 45px;
    background-color: #ffd800;
    border-radius: 5px;
    color: #fff;
    font-size: 17px;
    margin: 0px 0 10px;
    border: 0;
  }

  .login h3 {
    text-align: center;
    font-size: 19px;
    font-weight: 700;
    color: #333;
    letter-spacing: 1px;
    margin-bottom: 20px;
  }

  .guanbi {
    position: absolute;
    top: 25px;
    right: 25px;
    width: 12px;
    height: 12px;
    background: url("../../static/guanbi.png");
  }

  .tongyi span {
    color: #999;
  }

  .registe {
    position: absolute;
    bottom: 0;
    width: 400px;
    height: 50px;
    background-color: #ddd;
    text-align: center;
    line-height: 50px;
    font-size: 14px;
    letter-spacing: 1px;
  }

  .registe > p {
    font-weight: 700;
  }

  .registe > p > span {
    color: #999;
    font-weight: 400;
  }
</style>



